.App {
  height: 100vh;
  width: 100%;
  text-align: center;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 10% 80% 10%;
}

main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 20% 80%;
}
.interactive {
  width: 100%;
  height: 100%;
}
.interactive__button_red {
  background: #aa0114;
  width: 45%;
  height: 90%;
  border-radius: 50%;
}
.background {
  background: black;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.modal {
  width: 60%;
  height: 60%;
  z-index: 10;
  position: absolute;
  /* align-content: center; */
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  top: 20%;
  left: 20%;
}
.modal button {
  top: 30%;
  position: absolute;
  opacity: 0.1;
}
@media all and (min-width: 321px) and (max-width: 480px) {
  body {
    background: red;
  }
  .interactive__button_red {
    background: #aa0114;
    width: 85%;
    height: 85%;
    border-radius: 50%;
  }
}
